import Rellax from "rellax";

const whyPlantBasedFoodBlock = document.querySelector(".why-plant-based-food");

if (whyPlantBasedFoodBlock) {
    const parallaxImage_1 = whyPlantBasedFoodBlock.querySelector(".why-plant-based-food__circle");
    const parallaxImage_2 = whyPlantBasedFoodBlock.querySelector(".why-plant-based-food__leaf--top");
    const parallaxImage_3 = whyPlantBasedFoodBlock.querySelector(
        ".why-plant-based-food__leaf--bottom"
    );

    new Rellax(parallaxImage_1);
    new Rellax(parallaxImage_2);
    new Rellax(parallaxImage_3);
}
