import ConfettiGenerator from "confetti-js";

const joinPage = document.querySelector(".join-page");

if (joinPage) {
  const STEPS_NUMBER = 3;
  const joinForm = joinPage.querySelector(".join-page__form");
  const thanksBlock = joinPage.querySelector(".join-page__thanks-container");
  const nextButton = joinPage.querySelector(".join-page__next-button");
  const formFields = joinPage.querySelectorAll(".join-page__input-wrapper");

  let currentStep = 0;

  nextButton.addEventListener("click", () => {
    const currentField = document.querySelector(`#step-${currentStep}`);

    if (currentField.querySelector("input").value) {
      currentField.classList.remove("join-page__input-wrapper--with-error");
      currentStep++;

      if (currentStep < STEPS_NUMBER) {
        formFields.forEach((field) => field.classList.add("join-page__input-wrapper--hidden"));
        document
          .querySelector(`#step-${currentStep}`)
          .classList.remove("join-page__input-wrapper--hidden");
      } else {
        joinPage.classList.add("join-page--thanks-step");
        joinForm.classList.add("join-page__form--hidden");
        thanksBlock.classList.add("join-page__thanks-container--visible");

        const confettiSettings = {
          target: "confetti-canvas",
          max: "65",
          size: "1.5",
          animate: true,
          props: ["circle"],
          colors: [
            [96, 31, 107],
            [178, 222, 210],
            [255, 177, 60],
          ],
          clock: "80",
          rotate: false,
          start_from_edge: true,
          respawn: true,
        };
        const confetti = new ConfettiGenerator(confettiSettings);
        confetti.render();
      }
    } else {
      currentField.classList.remove("join-page__input-wrapper--with-error");
      setTimeout(() => {
        currentField.classList.add("join-page__input-wrapper--with-error");
      }, 200);
    }
  });
}
