import SmoothScroll from "smooth-scroll";

const header = document.querySelector(".header");

if (header) {
  const ESCAPE_CODE = 27;

  const toggleMenuButton = header.querySelector(".header__menu-button");
  const mobileCloseMenuButton = header.querySelector(".header__mobile-close-button");
  const joinLink = header.querySelector(".header__join-link");
  const menuWrapper = header.querySelector(".header__menu-outer-wrapper");
  const navItems = header.querySelectorAll(".header__nav-item");
  const navLinks = document.querySelectorAll(".header__nav-link[href*='#']");
  const joinPage = document.querySelector(".join-page");

  const vh = window.innerHeight * 0.01;
  document.documentElement.style.setProperty("--vh", `${vh}px`);

  if (joinPage) {
    joinLink.classList.add("header__menu-button--hidden");
  } else {
    joinLink.classList.remove("header__menu-button--hidden");

    navLinks.forEach((link) =>
      link.addEventListener("click", () => {
        toggleMenuButton.classList.remove("header__menu-button--opened");
        menuWrapper.classList.remove("header__menu-outer-wrapper--opened");

        if (window.matchMedia("(max-width: 768px)").matches) {
          toggleMenuButton.classList.remove("header__join-link--hidden");
          joinLink.classList.remove("header__menu-button--hidden");
        }
      })
    );

    new SmoothScroll(".header__nav-link[href*='#']");
  }

  toggleMenuButton.addEventListener("click", () => {
    toggleMenuButton.classList.toggle("header__menu-button--opened");
    menuWrapper.classList.toggle("header__menu-outer-wrapper--opened");

    if (window.matchMedia("(max-width: 768px)").matches) {
      toggleMenuButton.classList.toggle("header__join-link--hidden");
      if (!joinPage) {
        joinLink.classList.toggle("header__menu-button--hidden");
      }
    }
  });

  if (window.matchMedia("(max-width: 768px)").matches) {
    mobileCloseMenuButton.addEventListener("click", () => {
      toggleMenuButton.classList.remove("header__menu-button--opened");
      menuWrapper.classList.remove("header__menu-outer-wrapper--opened");

      if (window.matchMedia("(max-width: 768px)").matches) {
        toggleMenuButton.classList.remove("header__join-link--hidden");
        if (!joinPage) {
          joinLink.classList.remove("header__menu-button--hidden");
        }
      }
    });
  } else if (window.matchMedia("(min-width: 1200px)").matches) {
    window.addEventListener("keydown", (evt) => {
      if (evt.keyCode === ESCAPE_CODE) {
        toggleMenuButton.classList.remove("header__menu-button--opened");
        menuWrapper.classList.remove("header__menu-outer-wrapper--opened");
      }
    });

    navItems.forEach((item) => {
      item.addEventListener("mouseenter", () => {
        navItems.forEach((otherItem) => {
          otherItem.classList.remove("header__nav-item--active");
          otherItem.classList.add("header__nav-item--faded");
        });
        item.classList.remove("header__nav-item--faded");
        item.classList.add("header__nav-item--active");
      });

      item.addEventListener("mouseleave", () => {
        navItems.forEach((anotherItem) => {
          anotherItem.classList.remove("header__nav-item--active");
          anotherItem.classList.remove("header__nav-item--faded");
        });
      });
    });
  }
}
