import Rellax from "rellax";

const ourIdeasBlock = document.querySelector(".our-ideas");

if (ourIdeasBlock) {
    const plantImage_1 = ourIdeasBlock.querySelector(".our-ideas__first-plant");
    const plantImage_2 = ourIdeasBlock.querySelector(".our-ideas__second-plant");
    const plantImage_3 = ourIdeasBlock.querySelector(".our-ideas__third-plant");

    new Rellax(plantImage_1);
    new Rellax(plantImage_2);
    new Rellax(plantImage_3);
}
